import { forwardRef, ReactElement, Ref } from "react";
import {
  Dialog,
  DialogContent,
  DialogTitle,
  Divider,
  IconButton,
  Link,
  Slide,
  Typography,
} from "@mui/material";
import { TransitionProps } from "@mui/material/transitions";

import { LeftArrow } from "Assets/Icons";
import FilterPanel from "./FilterPanel";

type LeadFiltersProps = {
  open: boolean;
  doClose: () => void;
};

const SlideUpTransition = forwardRef(
  (
    props: TransitionProps & {
      children: ReactElement<any, any>;
    },
    ref: Ref<unknown>
  ) => <Slide direction="up" ref={ref} {...props} />
);

const LeadFiltersSX = {
  "& .MuiPaper-root": {
    borderRadius: 3.5,

    "& .MuiDialogTitle-root": {
      columnGap: 1.5,
      display: "flex",

      "& h3.MuiTypography-root": {
        color: "#24272C",
        fontSize: 20,
        fontWeight: 700,
        flexGrow: 1,
      },

      "& button.MuiLink-root": {
        color: "#24272C80",
        textDecorationColor: "#24272C80",
      },
    },

    "& .MuiDialogContent-root": {
      p: 0,
    },
  },
};

const LeadFilters = ({ open, doClose }: LeadFiltersProps) => {
  return (
    <Dialog
      open={open}
      TransitionComponent={SlideUpTransition}
      onClose={doClose}
      sx={LeadFiltersSX}
      aria-labelledby="filters"
      maxWidth="xs"
      fullWidth
    >
      <DialogTitle component="div">
        <IconButton onClick={doClose}>
          <LeftArrow />
        </IconButton>
        <Typography component="h3" id="filters">
          {"Filters"}
        </Typography>
        <Link component="button">CLEAR ALL</Link>
      </DialogTitle>
      <Divider />
      <DialogContent>
        <FilterPanel />
      </DialogContent>
    </Dialog>
  );
};

export default LeadFilters;
