import { Fragment } from "react";

import EmojiSelector from "./ChatAction/EmojiSelector";
import AttachmentSelector from "./ChatAction/AttachmentSelector";
import GeoLocationSelector from "./ChatAction/GeoLocationSelector";

const ChatAction = () => {
  return (
    <Fragment>
      <EmojiSelector />
      <AttachmentSelector />
      <GeoLocationSelector />
    </Fragment>
  );
};

export default ChatAction;
