import {
  Box,
  ClickAwayListener,
  Grow,
  InputAdornment,
  Popper,
  TextField,
  Typography,
} from "@mui/material";

import { useChatApi, useChatData } from "Contexts/Engage";
import {
  FlexRow,
  HalfColumn,
  HalfColumnSX,
} from "SharedComponents/CommonComponents";
import Img from "Components/Common/Img";
import { Search } from "Assets/Icons";

const GeoLocationSelector = () => {
  const h5 = {
    color: "#000000",
    fontSize: 14,
    fontWeight: 400,
    lineHeight: "16px",
    mb: 0.5,
  };
  const p = {
    color: "#24272C",
    fontSize: 12,
    fontWeight: 400,
    lineHeight: "18px",
    mb: 2,
  };
  const {
    // chatBoxWatch,
    currentAction,
    chatActionEl,
    mainContentRef,
  } = useChatData();
  const { setCurrentAction, setChatActionEl } = useChatApi();
  // const { message } = chatBoxWatch();
  const doClose = () => {
    setChatActionEl(null);
    setCurrentAction("");
  };

  return (
    <Popper
      open={currentAction === "geolocation"}
      anchorEl={chatActionEl}
      placement="top-start"
      transition
      sx={{ zIndex: 1 }}
    >
      {({ TransitionProps }) => (
        <Grow
          {...TransitionProps}
          timeout={350}
          style={{
            transformOrigin: "left bottom",
          }}
        >
          <Box
            sx={{
              bgcolor: "#ffffff",
              borderRadius: 1,
              boxShadow:
                "0px 2px 4px 0px #0000001A, 0px -4px 10px 0px #00000026",
              mb: 0.5,
              width: mainContentRef.current?.clientWidth - 45 ?? 450,
            }}
          >
            <ClickAwayListener onClickAway={doClose}>
              <Box p={1.5}>
                <FlexRow sx={{ columnGap: 2.25 }}>
                  <HalfColumn sx={HalfColumnSX}>
                    <Img src="/static/images/jpg/temp-map.jpg" alt="temp-map" />
                  </HalfColumn>
                  <HalfColumn sx={HalfColumnSX}>
                    <TextField
                      placeholder="Search your Location"
                      variant="standard"
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <Search />
                          </InputAdornment>
                        ),
                        sx: {
                          bgcolor: "#f2f3f7",
                          p: 1.25,
                          mb: 2,
                        },
                      }}
                    />
                    <Typography component="h5" sx={h5}>
                      FabIndia
                    </Typography>
                    <Typography component="p" sx={p}>
                      Prihvi Raj road, C-scheme, Jaipur
                    </Typography>
                    <Typography component="h5" sx={h5}>
                      Shoppers Paradise
                    </Typography>
                    <Typography component="p" sx={p}>
                      Jamuna lal bajaj marg, C-scheme, Jaipur
                    </Typography>
                    <Typography component="h5" sx={h5}>
                      Optus Media
                    </Typography>
                    <Typography component="p" sx={p}>
                      Gautam Marg, C-scheme, Jaipur
                    </Typography>
                  </HalfColumn>
                </FlexRow>
              </Box>
            </ClickAwayListener>
          </Box>
        </Grow>
      )}
    </Popper>
  );
};

export default GeoLocationSelector;
