import { Chip, Typography } from "@mui/material";
import {
  ChatArea,
  Message,
  MessageBody,
  MessageIn,
  MessageOut,
  MessageSx,
  TimeBadge,
} from "Containers/Engage/Chat/LayoutComponents";
import { formatMessage } from "Utils";

const ChatAreaComponent = () => {
  return (
    <ChatArea>
      <MessageOut>
        <Message className="message" sx={MessageSx}>
          <MessageBody>
            {formatMessage(
              "Here’s a list of ~*_--Honda--_*~ Scooter that will always keep you on the go!\n\nPlease select a product of your choice.\n\n\nReply 1 for Activa 125 \n\nReply 2 for Activa 6G \n\nReply 3 for Dio \n\nReply 4 for Grazia \n\nReply 5 for Activa 125 \n\n ____\n\n`🔁 Reply 0 to go back to main menu`\n`🔁 Reply # to go back`"
            )}
          </MessageBody>
          <Typography component="span">{"4:34 PM"}</Typography>
        </Message>
      </MessageOut>
      <MessageIn>
        <Message className="message" sx={MessageSx}>
          <MessageBody>
            {formatMessage(
              "Lorem ipsum dolor sit amet consectetur, adipisicing elit. Consectetur, aperiam obcaecati, autem accusamus quaerat veritatis vero reprehenderit veniam nulla modi ratione quia consequuntur odit debitis quibusdam reiciendis ipsum rerum suscipit?"
            )}
          </MessageBody>
          <Typography component="span">{"4:30 PM"}</Typography>
        </Message>
      </MessageIn>
      <MessageIn>
        <Message className="message" sx={MessageSx}>
          <MessageBody>{formatMessage("Dwarka Sector, 25")}</MessageBody>
          <Typography component="span">{"4:30 PM"}</Typography>
        </Message>
      </MessageIn>
      <MessageOut>
        <Message className="message" sx={MessageSx}>
          <MessageBody>{formatMessage("Area")}</MessageBody>
          <Typography component="span">{"4:30 PM"}</Typography>
        </Message>
      </MessageOut>
      <MessageIn>
        <Message className="message" sx={MessageSx}>
          <MessageBody>{formatMessage("Delhi")}</MessageBody>
          <Typography component="span">{"4:30 PM"}</Typography>
        </Message>
      </MessageIn>
      <MessageOut>
        <Message className="message" sx={MessageSx}>
          <MessageBody>{formatMessage("City")}</MessageBody>
          <Typography component="span">{"4:30 PM"}</Typography>
        </Message>
      </MessageOut>
      <MessageIn>
        <Message className="message" sx={MessageSx}>
          <MessageBody>{formatMessage("Rohit")}</MessageBody>
          <Typography component="span">{"4:30 PM"}</Typography>
        </Message>
      </MessageIn>
      <TimeBadge>
        <Chip size="small" label="Today" />
      </TimeBadge>
    </ChatArea>
  );
};

export default ChatAreaComponent;
